import React from "react";
import { Button, Col, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { setStaffData } from "../../../util/variable.global.tsx";
export const PrivateHome = () => {
  const navigate = useNavigate();
  const onNavigateClick = (event: string) => {
    if (event === "logout") {
      setStaffData(null);
      navigate('/');
    } else {
      navigate(event);
    }
  };

  return (
    <div className="containterForm">
      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <h3 className="titleForm">STAFF</h3>
        </Col>
      </Row>

      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <Button
            className="buttonSend"
            variant="outline-warning"
            onClick={() => onNavigateClick("/scanner-qr")}
          >
            ESCANEAR QR
          </Button>
        </Col>
      </Row>

      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <Button
            className="buttonSend"
            variant="outline-light"
            onClick={() => onNavigateClick("logout")}
          >
            CERRAR SESIÓN
          </Button>
        </Col>
      </Row>
    </div>
  );
};
