import { addDoc, collection, db, doc, getDocs, updateDoc } from "../../../service/firebaseConfig";

export async function createTicket(data: any) {
    try {
        const contadorCollection = collection(db, "Contador");
        const querySnapshot = await getDocs(contadorCollection);
        let newAvailableNumber = 0;
        querySnapshot.forEach(doc => {
            // Suponemos que el campo `availableNumber` existe en cada documento
            newAvailableNumber = doc.data().availableNumber;
        });
        // Crear el ticket en la colección "Entradas"
        const entradasCollection = collection(db, "Entradas");
        const result={
            name: data.userName,
            surname1: data.userSurname,
            phone: parseFloat(data.phone),
            vip: newAvailableNumber > 0 && newAvailableNumber <= 50, // Verificación de VIP
            idQR: "THERAVE" + data.phone.toString(),
            ID: data.phone
        }
        await addDoc(entradasCollection, {
            name: data.userName,
            surname1: data.userSurname,
            phone: parseFloat(data.phone),
            vip: newAvailableNumber > 0 && newAvailableNumber <= 50, // Verificación de VIP
            idQR: "THERAVE" + data.phone.toString(),
            ID: data.phone
        });

        // Si el número es mayor que 0, decrementamos 1
        if (newAvailableNumber > 0) {
            newAvailableNumber = newAvailableNumber - 1;
        }

        // Asegurarnos de que no sea negativo
        if (newAvailableNumber < 0) {
            newAvailableNumber = 0;
        }

        const contadorRef = doc(db, "Contador", "jmD3o5eFjjAa6HFG5rej"); // Asegúrate de usar el ID correcto del documento
        await updateDoc(contadorRef, {
            availableNumber: newAvailableNumber,
        });
        return result; // Retornar true si todo fue exitoso
    } catch (error) {
        console.error("❌ Error al guardar la entrada y actualizar el contador:", error);
        return false; // Retornamos false en caso de error
    }
}
