import React from "react";

import "./header.scss";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
export const Header = () => {
  return (
    <div className="containerHeader">
      <Row>
        <Col sm={{ span: 12, offset: 3 }}>
          <h1 className="titleHeader">LOGO</h1>
        </Col>
      </Row>
    </div>
  );
};
