import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import "./formTicket.scss";

const FormEditTicket = ({ onSubmit, initialData }) => {
    const [validated, setValidated] = useState(false);
    const [formData, setFormData] = useState({} as IForm);

    useEffect(() => {
        setFormData({
            ID: initialData?.ID,
            idQR: initialData?.idQR,
            name: initialData?.name,
            phone: initialData?.phone,
            surname1: initialData?.surname1,
            vip: initialData?.vip,
            assistance: initialData?.assistance || false,
            giftsConsumed: initialData?.giftsConsumed || false,
            drinkConsumed: initialData?.drinkConsumed || false,
        });
    }, [initialData]);

    const handleChange = (event) => {
        const { name, value, type, checked } = event.target;
        setFormData({
            ...formData,
            [name]: type === "checkbox" ? checked : value,
        });
    };

    const handleSubmit = (event) => {
        event.preventDefault(); // Prevent page reload
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        } else {
            onSubmit(formData);
        }
        setValidated(true);
    };

    return (
        <div className="containterForm">
            <Row>
                <Col xs={12} className="colTitle">
                    <h3 className="titleForm">Información entrada</h3>
                </Col>
            </Row>

            <Form noValidate validated={validated} onSubmit={handleSubmit}>
                <Row className="mb-3">
                    <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom01">
                        <Form.Label className="label">Nombre</Form.Label>
                        <Form.Control
                            className={"campo"}
                            required
                            type="text"
                            placeholder="Nombre"
                            name="userName"
                            value={formData.name}
                            disabled={true}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom02">
                        <Form.Label className="label">Apellidos</Form.Label>
                        <Form.Control
                            className={"campo"}
                            required
                            type="text"
                            placeholder="Apellidos"
                            name="userSurname"
                            value={formData.surname1}
                            disabled={true}
                        />
                    </Form.Group>

                    <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom03" className="phone">
                        <Form.Label className="label">Telefono</Form.Label>
                        <Form.Control
                            className={"campo"}
                            required
                            type="tel"
                            placeholder="Telefono"
                            name="phone"
                            value={formData.phone}
                            disabled={true}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs="6" sm="4" className="info2">
                        <Form.Check
                            onChange={handleChange}
                            label="Asistencia"
                            name="assistance"
                            checked={formData.assistance}
                            disabled={initialData.assistance}
                        />
                    </Form.Group>
                </Row>

                {formData.vip && (
                    <Row>
                        <Form.Group as={Col} xs="6" sm="4" className="info2">
                            <Form.Check
                                onChange={handleChange}
                                label="Regalos"
                                name="giftsConsumed"
                                checked={formData.giftsConsumed}
                                disabled={initialData.giftsConsumed}
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs="6" sm="4" className="info2">
                            <Form.Check
                                onChange={handleChange}
                                label="Bebida"
                                name="drinkConsumed"
                                checked={formData.drinkConsumed}
                                disabled={initialData.drinkConsumed}
                            />
                        </Form.Group>
                    </Row>
                )}

                <Form.Group className="sm-3 info">
                    <Form.Check
                        label="VIP"
                        name="vip"
                        checked={formData.vip}
                        disabled={true}
                    />
                </Form.Group>

                <Form.Group className="sm-3 rowButton">
                    <Button className="buttonSend" variant="outline-warning" type="submit">
                        Editar
                    </Button>
                </Form.Group>
            </Form>
        </div>
    );
};

export default FormEditTicket;

export interface IForm {
    ID: string;
    idQR: string;
    name: string;
    phone: string;
    surname1: string;
    vip: boolean;
    assistance: boolean;
    giftsConsumed: boolean;
    drinkConsumed: boolean;
}
